.card {
  cursor: pointer;
  margin-top: 50px;
  border: 1px solid #d6d6d6;

  .img-featured-creator {
    width: 100%;
  }

  .frame-bottom {
    padding: 16px;

    .card-title {
      font-weight: bold;
      font-size: 14px;
      margin-bottom: 0;
      height: 42px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 3; /* This specifies the number of lines to be displayed */
      -webkit-box-orient: vertical;
    }

    .by-creator {
      color: #999;
      font-size: 12px;
      height: 50px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 3; /* This specifies the number of lines to be displayed */
      -webkit-box-orient: vertical;
    }

    .card-creator-footer {
      align-items: stretch;
      display: flex;
      flex-direction: row;
      flex-grow: 1;
      justify-content: space-between;

      .parameter {
        .price {
          font-size: 18px;
          font-weight: bold;
        }

        .stars {
          font-size: 12px;
        }

        .sales {
          font-size: 12px;
          color: #999;
          line-height: 20px;
        }
      }

      .button-group {
        align-items: flex-end;
        display: flex;
        flex-direction: row;
        flex-grow: 0;

        .buttons {
          background: #fff;
          display: flex;
          height: 40px;
          justify-content: flex-end;
          position: relative;
          width: 100%;

          .shopping-cart {
            display: inline-block;
            width: 40px;
            fill: #666;
            background: #fff;
            border: 1px solid #ccc;
            border-radius: 2px;
            color: #666;
            cursor: pointer;
            flex-shrink: 0;
            font-size: 14px;
            font-weight: 700;
            height: 40px;
            line-height: 40px;
            position: relative;
            text-align: center;
            text-decoration: none;
          }

          .live-preview {
            --color-ui--custom-blue: #087096;
            -webkit-font-smoothing: antialiased;
            border: 1px solid var(--color-ui--custom-blue);
            border-radius: 2px;
            color: var(--color-ui--custom-blue);
            cursor: pointer;
            flex-grow: 1;
            font-size: 12px;
            font-weight: 700;
            height: 40px;
            line-height: 38px;
            margin: 0 4px;
            padding: 0 16px;
            text-decoration: none;
            white-space: nowrap;
          }
        }
      }
    }

  }
}

.card:hover {
  box-shadow: 0 0 16px #e6e6e6;
  transform: translateY(-2px);

  .img-frame {
    box-shadow: 0 0 16px #e6e6e6;
    opacity: 1;
  }
}