/* TourList.scss */

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

  .searchBar {
    border-radius: 10px;
    position: fixed;
    top: 69px; /* Adjust based on the height of your header */
    width: 75%; /* Ensure it takes full width minus some padding */
    max-width: 100%;
    z-index: 999; /* Slightly less than header to stay below */
    background-color: white; /* Set background color to avoid transparency issues */
    //padding: 30px 185px; /* Add some padding for better appearance */
    box-sizing: border-box; /* Ensure padding is included in width calculation */
    display: flex;
    align-items: center;
    border: 7px solid white;
    gap: 3px;
  }

  .content {
    margin-top: 150px; /* Adjust based on the combined height of your header and search bar */
    width: calc(100% - 32px);
  }
}



